$(function () {
  const eleWidth = $('.insta_box .frame li').innerWidth()

  let state = false
  let playOn = false
  let direction = 'left' //true/falseÍ∞Ä ÏïÑÎãàÎùº
  let bannerAuto //setInterval(Î¨¥ÌïúÎ∞òÎ≥µ) Ìï®ÏàòÎ•º Î©àÏ∂îÍ≤å ÌïòÍ∏∞ ÏúÑÌï¥ ÏÉÅÌÉú Î≥ÄÏàò ÏÇ¨Ïö©

  function play() {
    if (!playOn) {
      playOn = true //Î©àÏ∂∞Ï†∏ÏûàÎã§ ÌïòÎ©¥ trueÎ°ú Î∞îÍøîÏ£ºÍ≥†
      bannerAuto = setInterval(function () {
        if (direction === 'left') {
          $('.prev').click()
        } else {
          $('.next').click()
        }
      }, 2000)
    }

    if (playOn) {
      $('.frame li a').mouseover(function () {
        stop()
      })
    }
  } //play()

  function stop() {
    if (playOn) {
      playOn = false
      clearInterval(bannerAuto)
    }
    $('.frame li a').mouseout(function () {
      play()
    })
    $('.frame li a').click(function () {
      location.href
    })
  } //stop()


  function left() {
    stop()
    direction = 'left'
    $('.insta_box .frame').animate({
      left: eleWidth * -1
    }, 500, function () {
      $(this).children('li:first').insertAfter($(this).children('li:last'))
      $(this).css('left', 0)
      state = false //ÏÉÅÌÉúÍ∞Ä ÎÅùÎÇ¨Îã§
      play() //Îã§Ïãú ÌîåÎ†àÏù¥
    })
  } //left()

  function right() {
    stop()
    direction = 'right'
    $('.insta_box .frame li:last').insertBefore($('.frame li:first'))
    $('.insta_box .frame').css('left', eleWidth * -1)
    $('.insta_box .frame').animate({
      left: 0
    }, 500, function () {
      state = false
      play()
    })
  } //right

  $('.prev').click(function () {
    if (!state) {
      state = true //stateÍ∞Ä falseÎùºÎ©¥ trueÎ°ú Î∞îÍøîÏ£ºÍ≥† leftÎ≤ÑÌäºÏùÑ ÎàåÎü¨Ï£ºÍ∏∞
      left()
    }
  }) //prev_btn_click_events

  $('.next').click(function () {
    if (!state) {
      state = true
      right()
    }
  }) //next_btn_click_event

  $('.stop').click(function () {
    stop()
  }) //stop_btn_click_event

  $('.play').click(function () {
    play(0)
  }) //play_btn_click_event

  $('.play').click() //Ï¥àÍ∏∞ Í∞ïÏ†ú ÏûêÎèôÏã§Ìñâ

});